import { faInfo } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import React, { useEffect, useRef, useState } from "react";
import Button from "react-bootstrap/Button";
import Form from "react-bootstrap/Form";
import InputGroup from "react-bootstrap/InputGroup";
import OverlayTrigger from "react-bootstrap/OverlayTrigger";
import Tooltip from "react-bootstrap/Tooltip";
import { FormattedMessage } from "react-intl";

export type AnyEnum = {
    [key: string]: string | number;
    [index: number]: string;
};

export enum FieldType {
    Boolean = "boolean",
    Number = "number",
    String = "string",
    TextArea = "textarea",
    Password = "password",
    Enum = "enum"
}

export type InputFieldTypes = number | string | boolean;

export type InputFieldProps = {
    name: string;
    disabled?: boolean;
    tooltip?: string;
    type: FieldType;
    hideReadOnly?: boolean;
    additionalAppend?: React.ReactNode;
    forceChanged?: boolean;
} & (
    | {
          defaultValue?: boolean | null;
          onChange: (newValue: boolean, isValid: boolean) => unknown;
          type: FieldType.Boolean;
      }
    | {
          defaultValue?: number | null;
          onChange: (newValue: number, isValid: boolean) => unknown;
          type: FieldType.Number;
          min?: number;
          max?: number;
      }
    | {
          defaultValue?: string | null;
          onChange: (newValue: string, isValid: boolean) => unknown;
          type: FieldType.String;
      }
    | {
          defaultValue?: string | null;
          onChange: (newValue: string, isValid: boolean) => unknown;
          type: FieldType.TextArea;
      }
    | {
          defaultValue?: string | null;
          onChange: (newValue: string, isValid: boolean) => unknown;
          type: FieldType.Password;
      }
    | {
          defaultValue?: number | null;
          onChange: (newValue: number, isValid: boolean) => unknown;
          type: FieldType.Enum;
          noLocalize?: boolean;
          enum: AnyEnum;
      }
);

interface ControlProps {
    value: InputFieldTypes;
    onChange: (newvalue: InputFieldTypes) => unknown;
    disabled?: boolean;
    name: string;
}

const StringControl = React.forwardRef<HTMLInputElement, ControlProps>(
    function StringControl(props, ref): JSX.Element {
        return (
            <Form.Control
                value={props.value as string}
                onChange={e => props.onChange(e.target.value)}
                disabled={props.disabled}
                ref={ref}
            />
        );
    }
);

const TextAreaControl = React.forwardRef<HTMLInputElement, ControlProps>(
    function StringControl(props): JSX.Element {
        return (
            <Form.Control
                as="textarea"
                value={props.value as string}
                onChange={e => props.onChange(e.target.value)}
                disabled={props.disabled}
            />
        );
    }
);

const PasswordControl = React.forwardRef<HTMLInputElement, ControlProps>(
    function PasswordControl(props, ref): JSX.Element {
        return (
            <Form.Control
                value={props.value as string}
                onChange={e => props.onChange(e.target.value)}
                disabled={props.disabled}
                type="password"
                ref={ref}
            />
        );
    }
);

const BooleanControl = React.forwardRef<HTMLInputElement, ControlProps>(
    function BooleanControl(props, ref): JSX.Element {
        const rndId = Math.random().toString();
        return (
            <label
                htmlFor={rndId}
                className="d-flex m-0 flex-grow-1 justify-content-center align-content-center">
                <Form.Check
                    id={rndId}
                    checked={props.value as boolean}
                    onChange={e => props.onChange(e.target.checked)}
                    type="switch"
                    className="m-auto"
                    disabled={props.disabled}
                    ref={ref}
                />
            </label>
        );
    }
);

type NumberControlProps = ControlProps & {
    min?: number;
    max?: number;
};
const NumberControl = React.forwardRef<HTMLInputElement, NumberControlProps>(
    function NumberControl(props, ref): JSX.Element {
        return (
            <Form.Control
                value={props.value as number}
                onChange={(e: React.ChangeEvent<HTMLInputElement>) =>
                    props.onChange(
                        isNaN(e.target.valueAsNumber) ? e.target.value : e.target.valueAsNumber
                    )
                }
                disabled={props.disabled}
                min={props.min}
                max={props.max}
                type="number"
                ref={ref}
            />
        );
    }
);

type EnumControlProps = ControlProps & {
    enum: AnyEnum;
    noLocalize?: boolean;
};
const EnumControl = React.forwardRef<HTMLSelectElement, EnumControlProps>(
    function EnumControl(props, ref): JSX.Element {
        return (
            <Form.Control
                value={props.value as string}
                onChange={e => props.onChange(parseInt(e.target.value))}
                disabled={props.disabled}
                as="select"
                custom
                ref={ref}>
                {Object.entries(props.enum)
                    //filters out reverse mapping
                    .filter(([key]) => isNaN(parseInt(key)))
                    .map(([key, value]) => {
                        return props.noLocalize ? (
                            <option key={value} value={value}>
                                {key}
                            </option>
                        ) : (
                            <FormattedMessage id={`${props.name}.${key}`} key={key}>
                                {message => (
                                    <option key={value} value={value}>
                                        {message}
                                    </option>
                                )}
                            </FormattedMessage>
                        );
                    })}
            </Form.Control>
        );
    }
);

export const defaultValues: Record<FieldType, InputFieldTypes> = {
    [FieldType.Enum]: 0,
    [FieldType.Number]: 0,
    [FieldType.Boolean]: false,
    [FieldType.String]: "",
    [FieldType.Password]: "",
    [FieldType.TextArea]: ""
};

export default function InputField(props: InputFieldProps): JSX.Element {
    const [currentValue, setCurrentValue] = useState(
        props.defaultValue ?? defaultValues[props.type]
    );
    const controlRef = useRef<HTMLInputElement>(null);
    useEffect(() => {
        setCurrentValue(props.defaultValue ?? defaultValues[props.type]);
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [props.defaultValue]);

    useEffect(() => {
        if (controlRef.current) {
            if (controlRef.current.checkValidity()) {
                controlRef.current.classList.remove("is-invalid");
            } else {
                controlRef.current.classList.add("is-invalid");
            }
        }

        switch (props.type) {
            case FieldType.Boolean:
                props.onChange(
                    currentValue as boolean,
                    controlRef.current?.checkValidity() ?? true
                );
                return;
            case FieldType.Enum:
            case FieldType.Number:
                props.onChange(currentValue as number, controlRef.current?.checkValidity() ?? true);
                return;
            case FieldType.String:
            case FieldType.Password:
            case FieldType.TextArea:
                props.onChange(currentValue as string, controlRef.current?.checkValidity() ?? true);
                return;
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [currentValue]);

    const tooltip = (innerid?: string) => {
        if (!innerid) return <React.Fragment />;

        return (
            <Tooltip id={innerid}>
                <FormattedMessage id={innerid} />
            </Tooltip>
        );
    };

    const basicControls: {
        [Property in typeof props.type]: Property extends FieldType.Enum | FieldType.Number
            ? undefined
            : React.FC<ControlProps>;
    } = {
        string: StringControl,
        password: PasswordControl,
        boolean: BooleanControl,
        textarea: TextAreaControl,
        [FieldType.Number]: undefined,
        [FieldType.Enum]: undefined
    };

    const changed =
        currentValue != (props.defaultValue ?? defaultValues[props.type]) || props.forceChanged;

    return (
        <InputGroup>
            <OverlayTrigger
                overlay={tooltip(props.tooltip)}
                show={props.tooltip ? undefined : false}>
                {({ ref, ...triggerHandler }) => (
                    <InputGroup.Prepend className="w-50 w-xl-40">
                        <InputGroup.Text className="flex-grow-1" {...triggerHandler}>
                            <span className={changed ? "font-weight-bold" : ""}>
                                <FormattedMessage id={props.name} />
                            </span>
                            <div className="ml-auto">
                                {props.disabled && !props.hideReadOnly ? (
                                    <FormattedMessage id="generic.readonly" />
                                ) : null}
                                <div
                                    ref={ref}
                                    className="d-inline-block ml-2"
                                    style={{ visibility: props.tooltip ? "unset" : "hidden" }}>
                                    <FontAwesomeIcon icon={faInfo} />
                                </div>
                            </div>
                        </InputGroup.Text>
                    </InputGroup.Prepend>
                )}
            </OverlayTrigger>
            {props.type === FieldType.Number ? (
                <NumberControl
                    value={currentValue}
                    onChange={newValue => setCurrentValue(newValue)}
                    name={props.name}
                    disabled={props.disabled}
                    max={props.max}
                    min={props.min}
                    ref={controlRef}
                />
            ) : props.type === FieldType.Enum ? (
                <EnumControl
                    value={currentValue}
                    onChange={newValue => setCurrentValue(newValue)}
                    name={props.name}
                    enum={props.enum}
                    noLocalize={props.noLocalize}
                    disabled={props.disabled}
                />
            ) : (
                React.createElement<ControlProps & React.RefAttributes<HTMLInputElement>>(
                    basicControls[props.type],
                    {
                        value: currentValue,
                        onChange: newValue => setCurrentValue(newValue),
                        disabled: props.disabled,
                        name: props.type,
                        ref: controlRef
                    }
                )
            )}
            {!(!changed || props.disabled) || props.additionalAppend ? (
                <InputGroup.Append>
                    {!(!changed || props.disabled) ? (
                        <Button
                            variant="danger"
                            onClick={() =>
                                setCurrentValue(props.defaultValue ?? defaultValues[props.type])
                            }>
                            <FontAwesomeIcon icon="undo" />
                        </Button>
                    ) : null}
                    {props.additionalAppend}
                </InputGroup.Append>
            ) : null}
        </InputGroup>
    );
}
